"use client";
import React from "react";
import { useGetNewsQuery } from "@/redux/api/news";
import Image from "next/image";
import { LuMessagesSquare } from "react-icons/lu";
import { useRouter } from "next/navigation";
import { motion } from "framer-motion";
import scss from "./NewsMainContent.module.scss";

const NewsMainContent = () => {
  const { data } = useGetNewsQuery();
  const router = useRouter();

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
      },
    },
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
    },
  };

  return (
    <section className={scss.NewsMainContent}>
      <div className="container">
        <div className={scss.content}>
          <motion.div
            className={scss.news_head}
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <h1>Жаңылыктар</h1>
            <hr />
          </motion.div>
          <motion.div
            className={scss.news_cards}
            variants={containerVariants}
            initial="hidden"
            animate="visible"
          >
            {data?.map((item, index) => (
              <motion.div
                key={index}
                className={scss.news_card}
                variants={itemVariants}
                whileHover={{ scale: 1.03 }}
                transition={{ type: "spring", stiffness: 300 }}
              >
                <Image
                  onClick={() => router.push(`/news/${item.id}`)}
                  alt="news_img"
                  src={item.image}
                  width={700}
                  height={500}
                  priority
                  quality={70}
                />
                <h2>{item.description}</h2>
                <div className={scss.news_end}>
                  <p>{item.updated_at.slice(0, 10)}</p>
                  <motion.div whileHover={{ scale: 1.2 }}>
                    <LuMessagesSquare />
                  </motion.div>
                </div>
              </motion.div>
            ))}
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default NewsMainContent;
